.search__form {
  height: 24px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search__bar {
  position: relative;
  height: 24px;
}

.input__search {
  width: 300px;
  height: 24px;
  background: linear-gradient(0deg, #F6F6F6, #F6F6F6), #FEFEFE;
  border: 1px solid #E2E2E2;
  box-sizing: border-box;
  box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  padding-left: 30px;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
}

.input__icon {
  width: 12px;
  height: 12px;
  position: absolute;
  z-index: 40;
  top: 7px;
  left: 10px;
}

.search__button {
  width: 60px;
  height: 24px;
  background: linear-gradient(0deg, #F6F6F6, #F6F6F6), #FEFEFE;
  border: 1px solid #E2E2E2;
  box-sizing: border-box;
  box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.5s ease;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
}

.search__button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.search__result {
  position: absolute;
  top: 25px;
  left: 0px;
  width: 300px;
  /* height: 24px; */
  background: linear-gradient(0deg, #F6F6F6, #F6F6F6), #FEFEFE;
  border: 1px solid #E2E2E2;
  /* box-sizing: border-box; */
  box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  z-index: 10;
  /* padding-left: 25px; */
}

.result__item {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  border: 1px solid rgb(226, 226, 226);
  padding: 5px 5px 5px 30px;
  cursor: pointer;
}

.result__item:hover {
  background-color: antiquewhite;
}

